export default ".portfolio-mf {\n  background: #f5f5f5;\n}\n\n.wrapper {\n  max-width: 1080px;\n  margin: 50px auto;\n  padding: 0 20px;\n  position: relative;\n}\n\n.wrapper .center-line {\n  position: absolute;\n  height: 100%;\n  width: 4px;\n  background: #fff;\n  left: 50%;\n  top: 20px;\n  transform: translateX(-50%);\n}\n\n.wrapper .row {\n  display: flex;\n}\n\n.wrapper .row-1 {\n  justify-content: flex-start;\n}\n\n.wrapper .row-2 {\n  justify-content: flex-end;\n}\n\n.wrapper .row section {\n  background: #fff;\n  border-radius: 5px;\n  width: calc(50% - 40px);\n  padding: 20px;\n  position: relative;\n}\n\n.wrapper .row section::before {\n  position: absolute;\n  content: \"\";\n  height: 15px;\n  width: 15px;\n  background: #fff;\n  top: 28px;\n  z-index: -1;\n  transform: rotate(45deg);\n}\n\n.row section:hover {\n  transition: all 500ms ease;\n  box-shadow: 0 0 0 10px #cde1f8;\n}\n\n.row-1 section::before {\n  right: -7px;\n}\n\n.row-2 section::before {\n  left: -7px;\n}\n\n.row section .icon,\n.center-line .scroll-icon {\n  position: absolute;\n  background: #f2f2f2;\n  height: 40px;\n  width: 40px;\n  text-align: center;\n  line-height: 40px;\n  border-radius: 50%;\n  color: #0a2030;\n  font-size: 17px;\n  box-shadow: 0 0 0 4px #fff, inset 0 2px 0 rgba(0, 0, 0, 0.08), 0 3px 0 4px rgba(0, 0, 0, 0.05);\n}\n\n.center-line .scroll-icon {\n  bottom: 0px;\n  left: 50%;\n  font-size: 25px;\n  transform: translateX(-50%);\n}\n\n.title-box a {\n  text-decoration: none;\n  background: #0a2030;\n  color: #fff;\n  padding: 7px 15px;\n  border-radius: 5px;\n  font-weight: 400;\n  transition: all 0.3s ease;\n}\n\n.row-1 section .icon {\n  top: 15px;\n  right: -60px;\n}\n\n.row-2 section .icon {\n  top: 15px;\n  left: -60px;\n}\n\n.row section .details,\n.row section .bottom {\n  display: flex;\n  align-items: center;\n  justify-content: space-between;\n}\n\n.row section .details .title {\n  font-size: 22px;\n  font-weight: 600;\n}\n\n.row section p {\n  margin: 10px 0 17px 0;\n}\n\n.row section .bottom a {\n  text-decoration: none;\n  background: #0a2030;\n  color: #fff;\n  padding: 7px 15px;\n  border-radius: 5px;\n  font-weight: 400;\n  transition: all 0.3s ease;\n}\n\n.row section .bottom a:hover {\n  transform: scale(0.97);\n}\n\n@media (max-width: 790px) {\n  .wrapper .center-line {\n    left: 40px;\n  }\n\n  .wrapper .row {\n    margin: 30px 0 3px 60px;\n  }\n\n  .wrapper .row section {\n    width: 100%;\n  }\n\n  .row-1 section::before {\n    left: -7px;\n  }\n\n  .row-1 section .icon {\n    left: -60px;\n  }\n}\n\n@media (max-width: 440px) {\n  .wrapper .center-line,\n.row section::before,\n.row section .icon {\n    display: none;\n  }\n\n  .wrapper .row {\n    margin: 10px 0;\n  }\n}\n/*# sourceMappingURL=data:application/json;base64, */"